import React, { Component } from 'react';
import './App.css';
const emailRegex= RegExp(/^[a-zA-Z0-9.!#$%&'*+/=?^-{|}~`_']+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/);
class ControlledComponent extends Component {
  constructor() {
    super();
    this.state = {
      firstName: '',
      lastName: '',
      email: '',
      phone: '',
      comments:'',
      points:'',

      errors: {
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
        comments:'',
        points:'',
      },
    };
  }
 
  handleSubmit = async (e) => {
    e.preventDefault();
    
    var errKeys = await Object.keys(this.state).filter((key) => {

      if (this.state[key] === '' && this.state[key] !== 'errors') 
        return key;

    });

    if (errKeys.length >= 1) 
    {
        alert('Please fill all fields');
    }

    else{
    console.log(this.state);
    alert("form submitted sucessfully!")
    }
    
  };


  handleEmailValidation=(e)=>{
    
    var errors = { ...this.state.errors };
    if (e.target.value === '') 
    {
      errors[e.target.name] = 'Required';
    }
    else if(emailRegex.test(e.target.value)==false){
      errors[e.target.name] = 'not a valid';
    }
    else {
      errors[e.target.name] = '';
    }
    this.setState({ errors, [e.target.name]: e.target.value });
  };


  handlePhoneNumberValidation=(e)=>{
    var errors = { ...this.state.errors };
    if (e.target.value === '') 
    {
      errors[e.target.name] = 'Required';
    }
    else if (isNaN(e.target.value)) 
    {
      errors[e.target.name] = 'cannot contain letters';
      this.setState({[e.target.name]: '' });
    }
   else if (e.target.value.length<10) 
    {
      errors[e.target.name] = 'cannot be less than 10';
    }
    else {
      errors[e.target.name] = '';
    }
    this.setState({ errors, [e.target.name]: e.target.value });
  };


  handleChange = async (e) => {
    var errors = { ...this.state.errors };
     
  if (e.target.value === '') {
      errors[e.target.name] = 'Required';
    } 
   
    else {
      errors[e.target.name] = '';
    }

  await this.setState({ errors, [e.target.name]: e.target.value });
  };

  handleReset = () => {

    this.setState({
      firstName: '',
      lastName: '',
      email: '',
      phone:'',
      comments:'',
      points:'',

    });
  };

  
  render() {
    return (
      <>
        <div className="container">
          <h3> FeedBack Form </h3>
          <fieldset>
          <form onSubmit={(e) => this.handleSubmit(e)}>
            <div className="one">
              <label> First Name </label>
              <input className="two"
                type="text"
                name="firstName"
                value={this.state.firstName}
                onChange={(e) => this.handleChange(e)}
              />{' '}
              <br />
              <span style={{ color: 'red' }}>
                {' '}
                {this.state.errors.firstName}{' '}
              </span>
            </div>
            <br />

            <div className="one">
              <label> Last Name </label>
              <input className="two"
                type="text"
                name="lastName"
                value={this.state.lastName}
                onChange={(e) => this.handleChange(e)}
              />
              <br />
              <span style={{ color: 'red' }}>
                {' '}
                {this.state.errors.lastName}{' '}
              </span>
            </div>
            <br />

            <div className="one">
              <label> Email </label>
              <input
                type="email"
                name="email"
                value={this.state.email}
                onChange={(e) => this.handleEmailValidation(e)}
              />
              <br />
              <span style={{ color: 'red' }}> {this.state.errors.email} 
             

              </span>
            </div>
            <br />

            <div className="one">
            <label > Phone Number </label>
              <input className="two"
                type="text"
                name="phone"
                minLength='10'
                maxLength='10'
                value={this.state.phone}
                onChange={(e) => this.handlePhoneNumberValidation(e)}
                
                
              />{' '}
              <br />
              <span style={{ color: 'red' }}>
                {' '}
                {this.state.errors.phone}{' '}
              </span>
            </div>
            <br />

            <div>
              <label className="one"> Ratings </label>
              <input 
                type="radio"
                name="points"
                value="1"
                onChange={(e) => this.handleChange(e)}
              />{' '}
              1
              <input 
                type="radio"
                name="points"
                value="2"
                onChange={(e) => this.handleChange(e)}
              />{' '}
              2
              <input
                type="radio"
                name="points"
                value="2"
                onChange={(e) => this.handleChange(e)}
              />{' '}
              3
              <input
                type="radio"
                name="points"
                value="2"
                onChange={(e) => this.handleChange(e)}
              />{' '}
              4
              <input
                type="radio"
                name="points"
                value="2"
                onChange={(e) => this.handleChange(e)}
              />{' '}
              5
            </div>
            <br />

            <div>
              <label className="one"> Comments </label>
              <textarea className="two"
                type="text"
                name="comments"
                value={this.state.comments}
                onChange={(e) => this.handleChange(e)}
              />{' '}
              <br />
              <span style={{ color: 'red' }}>
                {' '}
                {this.state.errors.comments}{' '}
              </span>
            </div>
            <br />
    
            <button type="submit"> Submit </button> &nbsp;
            <button type="button" onClick={this.handleReset}>
              {' '}
              Reset
            </button>
          </form>
          </fieldset>
        </div>
      </>
    );
  }
}

export default ControlledComponent;